export default {
	data() {
		return {
			addressData: {
				name: '',
				phone: '',
				province: '北京市',
				city: '北京市',
				area: '东城区',
				address: '',
				is_default: false
			},
		}
	},
	onLoad(option){
		let title = '新增收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址';
			this.addressData = JSON.parse(option.data)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		RegionChange(e) {
			let region = e.detail.value
			this.addressData.province = region[0]
			this.addressData.city = region[1]
			this.addressData.area = region[2]
		},
		switchChange(e){
			this.addressData.is_default = e.detail.value;
		},
		
		//地图选择地址
		chooseLocation(){
			uni.chooseLocation({
				success: (data)=> {
					console.log(data)
					this.addressData.addressName = data.name;
					this.addressData.address = data.address;
				}
			})
		},
		
		//提交
		confirm(){
			let data = this.addressData;
			if(!data.name){
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)){
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			// if(!data.address){
			// 	this.$api.msg('请在地图选择所在位置');
			// 	return;
			// }
			// if(!data.area){
			// 	this.$api.msg('请填写门牌号信息');
			// 	return;
			// }
			var _this = this;
			this.$http.post('/store/api.member.address/set', data).then(res => {
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					_this.addressData = res.data.data
					this.$api.prePage()//获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
					this.$api.prePage().refreshList(_this.addressData, this.manageType);
					this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
					setTimeout(()=>{
						uni.navigateBack()
					}, 800)
				}
			}).catch(err => {
				console.log(err,333);
			})
			
		},
	}
}
